export const totalAmountPaid = (subscription, billingModel, billingModelType?) => {
  const bmType = BillingModelTypes[subscription?.billingModelType] || BillingModelTypes[billingModelType];

  switch (bmType) {
    case String(BillingModelTypes.Single):
    case String(BillingModelTypes.SingleDynamic):
      return subscription?.pullPayments[0]?.paymentAmount;

    case String(BillingModelTypes.Recurring):
    case String(BillingModelTypes.RecurringFreeTrial):
      return subscription?.amount * subscription?.pullPaymentIDs.length;

    case String(BillingModelTypes.RecurringPaidTrial):
      return subscription?.billingModel?.initialAmount
        ? Number(subscription?.billingModel.initialAmount)
        : Number(billingModel?.initialAmount) + Number(subscription?.amount) * subscription?.pullPaymentIDs.length;

    case String(BillingModelTypes.RecurringDynamic):
      return (
        Number(subscription?.amount) * Number(subscription?.pullPaymentIDs.length) +
        (Number(subscription?.initialAmount) ? Number(subscription?.initialAmount) : 0)
      );

    default: {
      return 0;
    }
  }
};

