export interface SubscriptionTableConfig {
  type: SubscriptionTableTypes;
  subscriptionsData: SerializedSubscriptionDetails[] | null;
  billingModel?: SerializedBillingModel | null;
  billingModelType?: BillingModelTypes | null;
}

const SubscriptionsTable = ({ type, subscriptionsData, billingModel, billingModelType }: SubscriptionTableConfig) => {
  const { account, chainId } = useActiveWeb3React();
  const { pathname } = useLocation();
  const history = useHistory();
  // console.log('subscriptionsData', subscriptionsData)

  const supportedTokens = useSupportedTokens();

  const [status, setStatus] = useState(null);

  const todayDate = Number(moment().unix());

  const [today, setToday] = useState(null);
  const [yesterday, setYesterday] = useState(null);
  const [week, setWeek] = useState(null);
  const [month, setMonth] = useState(null);
  const [dateRangeValue, setDateRangeValue] = useState(null);
  const [startDate, setStartDate] = useState(null);
  const [endDate, setEndDate] = useState(null);

  const [statusOptions, setStatusOptions] = useState([
    { id: 0, value: 'Status: All' },
    { id: 1, value: 'ACTIVE' },
    { id: 2, value: 'CANCELLED' },
    { id: 2, value: 'COMPLETED' },
  ]);

  const setStatusHandler = e => {
    let st = e.target.value;
    setStatus(st);
    setWeek(null);
    setMonth(null);
    setYesterday(null);
    setToday(null);
  };

  const getDaysOfMonth = () => {
    let y = new Date().getFullYear();
    let m = new Date().getMonth() + 1;

    const monthDate = moment(y + '-' + m, 'YYYY-MM');
    let daysInMonth = monthDate.daysInMonth();
    const arrDays = [];
    let date = new Date().getDate();

    while (daysInMonth) {
      const current = moment().date(daysInMonth);
      if (daysInMonth <= date) {
        arrDays.push(current.format('MM/DD/YYYY'));
      }
      daysInMonth--;
    }
    return arrDays;
  };

  let dateList = getDaysOfMonth();

  const dateFilters = (key, event = null) => {
    let date;

    setToday(null);
    setWeek(null);
    setMonth(null);
    setYesterday(null);
    setStatus(null);

    if (key === 'today') {
      date = moment().unix();
      setToday(Number(date));
    }
    if (key === 'yesterday') {
      date = moment().subtract(1, 'days').unix();
      setYesterday(Number(date));
    }
    if (key === 'week') {
      date = moment().subtract(1, 'week').unix();
      setWeek(Number(date));
    }
    if (key === 'month') {
      date = moment()
        .subtract(dateList.length - 1, 'month')
        .unix();
      setMonth(Number(date));
    }
    if (key === 'dateRange') {
      setStartDate(Number(moment(event?.[0]).unix()));
      setEndDate(Number(moment(event?.[1]).unix()));
      setDateRangeValue(event);
    }
  };

  const filterHandler = entry => {
    const dateForChecking = isAnyRecurringBM(
      type === SubscriptionTableTypes.MyCustomers
        ? BillingModelTypes[billingModelType]
        : BillingModelTypes[entry['billingModelType']],
    )
      ? Number(entry['startTimestamp'])
      : Number(entry['pullPayments'][0]['executionTimestamp']);
    if (status) {
      switch (status) {
        case 'ACTIVE':
          return (
            (String(entry.cancelTimestamp) === '0' || entry.cancelTimestamp === null) &&
            Number(entry.numberOfPayments) > 0
          );
        case 'CANCELLED':
          return Number(entry.cancelTimestamp) > 0;
        case 'COMPLETED':
          return Number(entry.numberOfPayments) === 0;
        default:
          return entry;
      }
    } else if (today) {
      return new Date(dateForChecking * 1000).getDate() === new Date(today * 1000).getDate();
    } else if (yesterday) {
      return new Date(dateForChecking * 1000).getDate() === new Date(yesterday * 1000).getDate();
    } else if (week) {
      return dateForChecking >= week && dateForChecking <= todayDate;
    } else if (month) {
      return dateForChecking >= month && dateForChecking <= todayDate;
    } else if (dateRangeValue) {
      return dateForChecking >= startDate && dateForChecking <= endDate;
    } else {
      return entry;
    }
  };

  const headers = [
    { label: 'Id', key: 'id' },
    { label: 'Wallet', key: 'Wallet' },
    { label: 'First Payment', key: 'First Payment' },
    { label: 'Last Payment', key: 'Last Payment' },
    { label: 'Next Payment', key: 'Next Payment' },
    { label: 'Status', key: 'Status' },
    { label: 'Total Amount', key: 'Total Amount' },
  ];
  const singleBillingHeaders = [
    { label: 'Id', key: 'id' },
    { label: 'Wallet', key: 'Wallet' },
    { label: 'First Payment', key: 'First Payment' },
    { label: 'Status', key: 'Status' },
    { label: 'Total Amount', key: 'Total Amount' },
  ];
  const singleDynamicBillingHeaders = [
    { label: 'Id', key: 'id' },
    { label: 'Name', key: 'Name' },
    { label: 'Wallet', key: 'Wallet' },
    { label: 'Status', key: 'Status' },
    { label: 'Total Amount', key: 'Total Amount' },
  ];
  const recurringDynamicBillingHeaders = [
    { label: 'Id', key: 'id' },
    { label: 'Name', key: 'Name' },
    { label: 'Wallet', key: 'Wallet' },
    { label: 'Status', key: 'Status' },
    { label: 'Total Amount', key: 'Total Amount' },
  ];

  const csvReport = {
    data: subscriptionsData,
    headers:
      billingModelType === BillingModelTypes.Single
        ? singleBillingHeaders
        : billingModelType === BillingModelTypes.SingleDynamic
        ? singleDynamicBillingHeaders
        : billingModelType === BillingModelTypes.RecurringDynamic
        ? recurringDynamicBillingHeaders
        : headers,
    filename: 'subscriptions.csv',
  };

  const mySubscriptionHeaders = [
    'Unique Ref. Id',
    'Name',
    'Type',
    'No. of Payments',
    'Price',
    'Subscription Date',
    'Last Payment',
    'Next Payment',
    'Status',
    'Total Amount',
    'Action',
  ];

  const freeTrialBMHeaders = [
    'Unique Ref. Id',
    'Wallet',
    'No. of Payments',
    'Trial Period',
    'First Payment',
    'Last Payment',
    'Next Payment',
    'Status',
    'Total Amount',
  ];

  const paidTrialBMHeaders = [
    'Unique Ref. Id',
    'Wallet',
    'No. of Payments',
    'Trial Period',
    'Initial Amount',
    'First Payment',
    'Last Payment',
    'Next Payment',
    'Status',
    'Total Amount',
  ];

  const recurringDynamicBMHeaders = [
    'Unique Ref. Id',
    'Name',
    'Wallet',
    'Trial Type',
    'First Payment',
    'Last Payment',
    'Next Payment',
    'Status',
    'Total Amount',
  ];
  const myCustomersHeaders = [
    'Unique Ref. Id',
    'Wallet',
    'No. of Payments',
    'First Payment',
    'Last Payment',
    'Next Payment',
    'Status',
    'Total Amount',
  ];
  const singleDynamicBMHeaders = ['Unique Ref. Id', 'Name', 'Wallet', 'Payment Date', 'Total Amount'];
  const singleBMHeaders = ['Unique Ref. Id', 'Wallet', 'Payment Date', 'Total Amount'];
  const tableHeaders = (): string[] => {
    switch (billingModelType) {
      case String(BillingModelTypes.Single):
        return singleBMHeaders;
      case String(BillingModelTypes.SingleDynamic):
        return singleDynamicBMHeaders;
      case String(BillingModelTypes.RecurringDynamic):
        return recurringDynamicBMHeaders;
      case String(BillingModelTypes.Recurring):
        return myCustomersHeaders;
      case String(BillingModelTypes.RecurringPaidTrial):
        return paidTrialBMHeaders;
      case String(BillingModelTypes.RecurringFreeTrial):
        return freeTrialBMHeaders;
      default: {
        return mySubscriptionHeaders;
      }
    }
  };

  const handleClick = ({ id, billingModelId }) => {
    if (type === SubscriptionTableTypes.MyCustomers) {
      history.push({
        pathname: `/merchant/transaction-details`,
        search: `?billing-model-type=${billingModelType}&billing-model-id=${billingModelId}&subscription-id=${id}`,
      });
    }
  };

  const [rowCount, setRowCount] = useState<number>(10);
  const [currentPage, setCurrentPage] = useState(1);
  const [counter, setCounter] = useState<number>(1);

  const currentData = data => {
    const begin = (currentPage - 1) * rowCount;
    const end = begin + rowCount;
    return data?.slice(begin, end);
  };

  const next = () => {
    const maxPage = Math.ceil((subscriptionsData?.length || 1) / rowCount);
    if (counter + rowCount < subscriptionsData?.length) {
      setCounter(counter + rowCount);
    }
    setCurrentPage(currentPage => Math.min(currentPage + 1, maxPage));
  };

  const prev = () => {
    if (counter != 1) {
      setCounter(counter - rowCount);
    }
    setCurrentPage(currentPage => Math.max(currentPage - 1, 1));
  };

  const [showCancelModal, setShowCancelModal] = useState(false);
  const [cancelSubscriptionId, setCancelSubscriptionId] = useState(null);
  const [cancelSubscriptionBillingModelType, setCancelSubscriptionBillingModelType] = useState(null);

  const cancelSubscriptionHandler = (id: string, bmType: BillingModelTypes) => {
    setCancelSubscriptionId(id);
    setCancelSubscriptionBillingModelType(bmType);
    setShowCancelModal(true);
  };

  const [isCancelBtnLoading, setIsCancelBtnLoading] = useState(false);

  return (
    <MainContent>
      <TableTopWrap className="table-top">
        <h3>{type === SubscriptionTableTypes.MySubscriptions ? 'My Subscriptions' : 'Customers'}</h3>

        <div className="select-drop">
          {type === SubscriptionTableTypes.MySubscriptions ||
            (isAnyRecurringBM(BillingModelTypes[billingModelType]) && (
              <select onChange={setStatusHandler}>
                {statusOptions?.map((value, index) => {
                  return (
                    <option key={index} value={value.value}>
                      {value.value}
                    </option>
                  );
                })}
              </select>
            ))}

          <DateRangePicker
            monthPlaceholder="MM"
            dayPlaceholder="DD"
            yearPlaceholder="YYYY"
            onChange={e => dateFilters('dateRange', e)}
            value={dateRangeValue}
          />

          <button className="table-top-btn" onClick={() => dateFilters('today')}>
            Today
          </button>

          <button className="table-top-btn" onClick={() => dateFilters('yesterday')}>
            Yesterday
          </button>

          <button className="table-top-btn" onClick={() => dateFilters('week')}>
            Week
          </button>

          <button className="table-top-btn" onClick={() => dateFilters('month')}>
            Month
          </button>

          {/*<div style={{ margin: "10px 0 0 10px" }}>*/}
          {/*  Export{" "}*/}
          {/*  <CSVLink className="csv-link" {...csvReport}>*/}
          {/*    CSV*/}
          {/*  </CSVLink>*/}
          {/*</div>*/}
        </div>
      </TableTopWrap>

      <StyledTable>
        <thead>
          <tr>
            {tableHeaders().map((title, index) => (
              <th key={index}>{title}</th>
            ))}
          </tr>
        </thead>
        <tbody>
          {subscriptionsData === null ? (
            <tr>
              <td colSpan={12}>
                <div>
                  <Loader className="loader-main" size={'3rem'} />
                </div>
              </td>
            </tr>
          ) : subscriptionsData?.filter(filterHandler)?.length > 0 ? (
            currentData(subscriptionsData?.filter(filterHandler)).map((row, index) => (
              <tr key={index}>
                <td> {row?.['uniqueReference']}</td>

                {(type === SubscriptionTableTypes.MySubscriptions ||
                  isDynamicBM(BillingModelTypes[billingModelType])) && (
                  <td>
                    {isDynamicBM(BillingModelTypes[row.billingModelType]) && !row?.name
                      ? 'Dynamic'
                      : row?.billingModel?.name
                      ? row?.billingModel?.name
                      : row?.name}
                  </td>
                )}

                {type === SubscriptionTableTypes.MySubscriptions && (
                  <td>{BillingModelTypeTitles[BillingModelTypes[row?.billingModelType]]}</td>
                )}

                {type === SubscriptionTableTypes.MyCustomers && (
                  <td
                    style={{ cursor: 'pointer' }}
                    onClick={() =>
                      handleClick({
                        id: row?.['id'],
                        billingModelId: row?.['billingModelId'] !== null ? row?.['billingModelId']! : billingModel?.id,
                      })
                    }
                  >
                    <StyledLink className="active">{row?.subscriber}</StyledLink>
                  </td>
                )}

                {String(BillingModelTypes.RecurringDynamic) == BillingModelTypes[billingModelType] ? (
                  <td>{BMTrialTypesLable[billingModel?.trialType]}</td>
                ) : isAnyRecurringBM(BillingModelTypes[billingModelType]) ||
                  isAnyRecurringBM(BillingModelTypes[row.billingModelType]) ? (
                  <td>
                    {row.pullPaymentIDs.length}/
                    {!isDynamicRecurring(BillingModelTypes[billingModelType]) &&
                    type === SubscriptionTableTypes.MyCustomers
                      ? billingModel?.numberOfPayments
                      : row?.billingModel?.numberOfPayments}
                    {isDynamicRecurring(BillingModelTypes[billingModelType]) &&
                    type === SubscriptionTableTypes.MyCustomers
                      ? billingModel?.totalPayments
                      : row?.totalPayments}
                  </td>
                ) : (
                  type === SubscriptionTableTypes.MySubscriptions && <td>1/1</td>
                )}

                {type === SubscriptionTableTypes.MySubscriptions &&
                  (isDynamicBM(BillingModelTypes[row.billingModelType]) ? (
                    <td>Dynamic price</td>
                  ) : (
                    <td>
                      {fromTokenDecimals(
                        row?.amount ? row?.amount.toString() : billingModel?.amount.toString(),
                        supportedTokens?.[row.settlementToken]?.decimals.toString(),
                      ).toString()}
                      &nbsp;
                      {supportedTokens?.[row.settlementToken]?.symbol}
                      <span>&nbsp; in {supportedTokens?.[row.paymentToken]?.symbol}</span>
                    </td>
                  ))}

                {type === SubscriptionTableTypes.MyCustomers &&
                  (isRecurringWithTrial(BillingModelTypes[billingModelType]) ||
                    isRecurringWithTrial(BillingModelTypes[row.billingModelType])) && (
                    <td>
                      {humanizeDuration(Number(billingModel?.trialPeriod) * 1000, {
                        largest: 1,
                        round: true,
                      })}
                    </td>
                  )}

                {type === SubscriptionTableTypes.MyCustomers &&
                  (BillingModelTypes[billingModel.billingModelType] === BillingModelTypes.RecurringPaidTrial ||
                    BillingModelTypes[billingModelType] === BillingModelTypes.RecurringPaidTrial) && (
                    <td>
                      {fromTokenDecimals(
                        billingModel?.initialAmount.toString(),
                        supportedTokens?.[row.settlementToken]?.decimals.toString(),
                      ).toString()}
                      &nbsp;
                      {supportedTokens?.[row.settlementToken]?.symbol}
                    </td>
                  )}

                {isAnyRecurringBM(BillingModelTypes[billingModelType]) ||
                isAnyRecurringBM(BillingModelTypes[row.billingModelType]) ? (
                  <td>
                    {isRecurringFreeTrial(BillingModelTypes[row.billingModelType]) ||
                    isRecurringFreeTrial(BillingModelTypes[billingModelType])
                      ? row?.pullPayments[0]?.executionTimestamp
                        ? moment(Number(row?.pullPayments[0]?.executionTimestamp) * 1000).format('lll')
                        : 'No payment yet'
                      : moment(Number(row?.startTimestamp) * 1000).format('lll')}
                  </td>
                ) : (
                  <td>{moment(Number(row?.pullPayments?.[0]?.executionTimestamp) * 1000).format('lll')}</td>
                )}
                {isAnyRecurringBM(BillingModelTypes[billingModelType ? billingModelType : row?.billingModelType]) ? (
                  (BillingModelTypes[billingModelType ? billingModelType : row?.billingModelType] ===
                    BillingModelTypes.RecurringFreeTrial ||
                    BillingModelTypes[billingModelType ? billingModelType : row?.billingModelType] ===
                      BillingModelTypes.RecurringFreeTrial ||
                    BillingModelTypes[billingModelType ? billingModelType : row?.billingModelType] ===
                      BillingModelTypes.RecurringDynamic) &&
                  Number(row?.lastPaymentTimestamp) === 0 ? (
                    <td>No initial payment yet</td>
                  ) : (
                    <td> {moment(Number(row?.lastPaymentTimestamp) * 1000).format('lll')}</td>
                  )
                ) : (
                  type === SubscriptionTableTypes.MySubscriptions && (
                    <td> {moment(Number(row?.pullPayments?.[0]?.executionTimestamp) * 1000).format('lll')}</td>
                  )
                )}

                {isAnyRecurringBM(BillingModelTypes[billingModelType]) ||
                isAnyRecurringBM(BillingModelTypes[row.billingModelType]) ? (
                  <td>
                    {Number(row?.cancelTimestamp) !== 0 || Number(row?.numberOfPayments) === 0
                      ? 'No furture payment'
                      : moment(Number(row?.nextPaymentTimestamp) * 1000).format('lll')}
                  </td>
                ) : (
                  type === SubscriptionTableTypes.MySubscriptions && <td>No future payment</td>
                )}

                {isAnyRecurringBM(BillingModelTypes[billingModelType]) ||
                isAnyRecurringBM(BillingModelTypes[row.billingModelType]) ? (
                  <>
                    {Number(row?.numberOfPayments ? row?.numberOfPayments : row?.totalPayments) > 0 &&
                    (row?.totalPayments ? row?.totalPayments > row?.pullPaymentIDs.length : true) ? (
                      <>
                        {Number(row?.cancelTimestamp) > 0 ? (
                          <td>
                            {' '}
                            CANCELLED
                            <br />
                            <span style={{ color: '#999', fontSize: '0.7rem' }}>
                              by{' '}
                              {String(row?.cancelledBy.toLowerCase()) ===
                              String(KEEPER_REGISTRY_ADDRESS[chainId].toLowerCase())
                                ? 'Low balance'
                                : (row?.cancelledBy === account && pathname.includes('customer')) ||
                                  (row?.cancelledBy !== account && pathname.includes('merchant'))
                                ? 'Customer'
                                : 'Merchant'}
                            </span>{' '}
                          </td>
                        ) : (
                          <td> ACTIVE </td>
                        )}
                      </>
                    ) : (
                      <td> COMPLETED </td>
                    )}
                  </>
                ) : (
                  <>{type === SubscriptionTableTypes.MySubscriptions && <td> COMPLETED </td>}</>
                )}

                {
                  <td>
                    {totalAmountPaid(row, billingModel, billingModelType) /
                      10 ** Number(supportedTokens?.[row.settlementToken]?.decimals)}
                    &nbsp;
                    {supportedTokens?.[row.settlementToken]?.symbol}
                    {type === SubscriptionTableTypes.MySubscriptions && (
                      <span>&nbsp; in {supportedTokens?.[row.paymentToken]?.symbol}</span>
                    )}
                  </td>
                }

                {type === SubscriptionTableTypes.MySubscriptions && (
                  <td>
                    <ButtonPrimary
                      className="action-btn"
                      onClick={() => {
                        cancelSubscriptionHandler(row?.['id'], BillingModelTypes[row?.['billingModelType']]);
                      }}
                      disabled={
                        Number(row?.numberOfPayments ? row?.numberOfPayments : row?.totalPayments) === 0 ||
                        Number(row?.cancelTimestamp) > 0 ||
                        (row?.totalPayments ? Number(row?.totalPayments) === row?.pullPaymentIDs.length : false)
                      }
                    >{`Cancel`}</ButtonPrimary>
                  </td>
                )}
              </tr>
            ))
          ) : (
            <tr>
              <td colSpan={12}>
                <div className="empty-table-row">
                  <span>No Subscriptions</span>
                </div>
              </td>
            </tr>
          )}
        </tbody>
      </StyledTable>

      <div className="pagination">
        <span>
          Show:
          <select
            onChange={e => {
              setRowCount(parseInt(e.target.value));
              setCurrentPage(1);
            }}
          >
            <option value="10">10 Rows</option>
            <option value="25">25 Rows</option>
            <option value="50">50 Rows</option>
            <option value="100">100 Rows</option>)
          </select>
        </span>
        <div className="navigation">
          <p onClick={prev}>
            <img src={Arrow} id="navigationOne" alt="arrows" />
          </p>
          <p onClick={next}>
            <img src={Arrow} alt="arrows" />
          </p>
        </div>
      </div>

      <CancelSubscriptionModal
        cancelSubscriptionId={cancelSubscriptionId}
        billingModelType={cancelSubscriptionBillingModelType}
        showCancelModal={showCancelModal}
        setShowCancelModal={setShowCancelModal}
        isLoading={isCancelBtnLoading}
        setIsLoading={setIsCancelBtnLoading}
      />
    </MainContent>
  );
};

export default SubscriptionsTable;
